.gameContainer {
  background-color: #0f0f0f;
  grid-column: 1 / -1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 200px;
  height: calc(100vh - 284px); }

.gameWrapper {
  position: relative; }

.gameControls {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  width: 768px;
  height: 432px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.buttonsContainer {
  width: 200px; }

.game {
  width: 768px;
  height: 432px; }

.spinnerContainer {
  height: calc(100vh - 64px - 104px);
  width: 100%;
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff; }

.loadingText {
  font-family: "HelveticaNeueW01-55Roma", "HelveticaNeueW02-55Roma", "HelveticaNeueW10-55Roma", "Helvetica Neue", "Helvetica", "Arial", "\30E1\30A4\30EA\30AA, meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   pro w3", "hiragino kaku gothic pro", "sans-serif";
  font-size: 24px; }

.aboutGameButton {
  background-color: transparent;
  border: 2px solid #e06154;
  color: #e06154;
  margin-top: 25px; }

.triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #e06154 transparent transparent transparent;
  margin-left: 5px; }



